import './Info.css';
import React, { useState } from 'react';
import OkImg from './ok.png';

const data = {
  dearguest: " !",
  info: "   ,  29.09.2023        -   !         .",
  wearewaitingyou: " !",
  data: "29.09.2023  14:00",
  yours: "   ",
  confirmations: ",    !        ;)",
  discard: " "
}

function Info(props) {
  const [showConfirmation, setShowConfirmation] = useState(true);

  const toggleConfirmation = () => {
    setShowConfirmation(prevState => !prevState);
  }

  const confirmationText = showConfirmation ? (
    <div className="defaultvision" style={{ display: 'flex', flexDirection: "row" }}>
      <div className='confirmation-img'>
        <img src={OkImg} className="OkIMg" alt="okimg" />
      </div>
      <div className='confirmation-text' >{data.confirmations}</div>
    </div>
  ) : (
    <div className='ochenjal' onClick={toggleConfirmation}> . ,   ,          </div>
  );

  const confirmationButton = showConfirmation ? (
    <div className='confirmation-btn' onClick={() => toggleConfirmation()}>{data.discard}</div>
  ) : (
    <div className='confirmation-btn' onClick={() => toggleConfirmation()}> </div>
  );

  return (
    <div className="info-container">
      <div className='backgroind-img'></div>
      <div className='white-background'>
        <div className='info-main-widht'>
          <div className='info-dearquest'>{props.prefix} {props.name}</div>
          <div className='info-main-info'>{data.info}</div>
          <div className='info-wearewaitingyou'>{data.wearewaitingyou}</div>
          <div className='info-maindata'>{data.data}</div>
          <div className='info-yours'>{data.yours}</div>
          <div className='confirmation-wrapper'>
            {confirmationText}
            {confirmationButton}
          </div>
        </div>
      </div>
    </div>
  );
}


export default Info;
